<template>
  <div id="box">
    <img src="@/assets/images/my/myhotel_bg.png" alt="" class="pos_img">
    <!-- <div class="date">
      <div class="month">{{month}}月</div>
      <van-cell :title="year+'年'" arrow-direction="down" is-link @click="showtime=true"/>
    </div> -->
    <div class="info">
      <ul>
        <li><strong>{{total}}</strong></li>
        <li>总业绩(元)</li>
      </ul>
    </div>
    <div class="list">
      <div class="item" v-for="(item, i) in list" :key="i">
        <div class="top">
          <img :src="item.head_pic" alt="" class="icon">
          <div class="left">
            <strong class="name">{{item.hotel_name}}</strong>
          </div>
        </div>
        <div class="bottom">
          <ul>
            <li>设备数量</li>
            <li>{{item.num}}</li>
          </ul>
          <ul>
            <li>销售业绩</li>
            <li>{{item.money}}</li>
          </ul>
        </div>
      </div>
      <!-- <div class="item">
        <div class="top">
          <img src="@/assets/images/mall/mall_cart.png" alt="" class="icon">
          <div class="left">
            <strong class="name">爱的故事上集</strong>
          </div>
        </div>
        <div class="bottom">
          <ul>
            <li>个人业绩</li>
            <li>1000.00</li>
          </ul>
          <ul>
            <li>团队业绩</li>
            <li>2000.00</li>
          </ul>
        </div>
      </div> -->
    </div>
    <!-- <van-popup 
      v-model="showtime"
      position="bottom"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="year-month"
        @confirm="gettime"
        @cancel="cancel"
        @change="change"
        :formatter="formatter"/>
    </van-popup> -->
    <div class="nomore" v-if="list.length==0">暂无数据</div>
  </div>
</template>

<script>
import { getmy_hotel } from '@/axios/getData';
import { Cell, DatetimePicker, Popup } from "vant";
export default {
  data(){
    return{
      year:new Date().getFullYear(),
      month:new Date().getMonth()+4,
      currentDate: new Date(),
      showtime:false,
      count:'',
      list:[],
      total:'',
      page:1
    }
  },
  components: {
    vanCell: Cell,
    vanDatetimePicker: DatetimePicker,
    vanPopup: Popup
  },
  created() {
    this.getdata();
  },
  methods:{
    async getdata(){
      var res = await getmy_hotel({page: this.page});
      this.count = res.data.count
      this.list = res.data.list
      this.total = res.data.total
      // this.num = res.data.num
      // this.pages = res.data.pages
      console.log(res.data,777)
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`;
      } else if (type === 'month') {
        return `${value}月`
      }
      return value;
    },
    //  时间弹出层    点击确定
    gettime(value,a,b){
      this.showtime = false
      const d = new Date(value);
      this.year = d.getFullYear();
      this.month = d.getMonth() + 1
      console.log(this.year, this.month);
      
    },
    //  时间弹出层    点击取消
    cancel(){
      this.showtime = false
    },
    //  时间弹出层    时间改变
    change(picker,a,b){
      console.log(picker,a,b);
    }
  },
}
</script>
<style scoped lang="scss">
#box{
  background:#FAFAFA;
  height:100vh;
  box-sizing: border-box;
}
/deep/ .van-cell{
  background: transparent;
  color:#fff;
  width:unset;
}
/deep/ .van-cell__title{
  flex:unset;
  font-size: 14pt;
}
  .pos_img{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    z-index: 0;
  }
  .date{
    position: absolute;
    display: flex;
    align-items: center;
    top:20pt;
    left:5%;
    z-index: 1;
    color:#fff;
    .month{
      width:45pt;
      font-size: 20pt;
      font-weight: 600;
    }
  }

  /* 我的卡片信息 */
  .info{
    margin-top:15pt;
    position: absolute;
    left:50%;
    transform: translateX(-50%);
    color:#fff;
    font-size: 12pt;
    text-align: center;
    &>ul>li:nth-child(1){
      font-size: 18pt;
    }
  }
  .icon1{
    margin-right:10pt;
    padding:2pt;
    border:1pt solid #F5A623;
    color:#F5A623;
    font-size: 8pt !important;
    border-radius: 4pt;
  }

  /* 成员列表数据 */
  .list{
    margin:5%;
    background: #fff;
    padding-top:90pt;
    .item{
      // display:flex;
      // width:100%;
      // justify-content: space-between;
      border-bottom:1pt solid #DEDEDE;
      padding-top:20pt;
      .top{
        width:100%;
        display: flex;
        align-items: center;
        // justify-content: space-between;
        .left{
          display: flex;
          align-items: center;
          flex: 1;
          .name{
            display: inline-block;
            margin-right:10pt;
            font-size: 11pt;
          }
        }
        .icon{
          margin:0 10pt;
          width:34pt;
          height:34pt;
          border-radius: 50%;
        }
      }
      .bottom{
        display: flex;
        justify-content:space-around;
        li{
          text-align: center;
        }
        ul>li:nth-child(2){
          color:#FF4C24;
          font-weight: 600;
          font-size: 12pt;
          margin:5pt 0;
        }
      }
    }
  }
</style>



